@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap');

/*      UTILITY     */

:root {
  --primary-blue: #006994;
  --primary-orange: #942B00;
}

body,
html {
    height: 100%;
    font-family: 'Roboto', sans-serif;
    background-color: var(--primary-blue);
}


.flex {
  display: flex;
}

a {
  color: black;
  text-decoration: none; /* no underline */
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1.15rem;
}

/*      NAVBAR      */
.navbar-container {
  background-color: var(--primary-blue);
  height: 100px;
  align-items: center;
  justify-content: center;
  border-bottom: 2px solid white;
}

.navbar-background {
  background-color: white;
  width: 100%;
  height: 50%;
}

.navbar {
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  width: 65%;
  height: 100%;
}

.navlogo {
  align-items: center;
  height: 100%;
}

.navlogo h4 {
  margin-left: 10px;
  color: var(--primary-blue);
  font-weight: bold;
}

.navlogo img {
  height: 95%;
}

.nav a {
  margin-right: 100px;
}

.nav a:active {
  font-weight: bold;
  color: var(--primary-blue);
}

/*      THUMBNAIL     */

.thumbnail-container {
  position: relative;
  width: 100%;
  height: 250px;
  justify-content: center;
  align-items: flex-end;
}

.thumbnail-container::before {
  content: '';
  position: absolute;
  background-image: url('./Images/thumbnail1.jfif');
  background-color: var(--primary-blue);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.75;
  width: 100%;
  height: 100%;
}

.thumbnail-container h2 {
  position: relative;
  text-align: center;
  color: white;
  font-weight: lighter;
}

.thumbnail-text {
  max-width: 600px;
  margin-bottom: 20px;
}

/*      SHOWCASE      */

.showcase-container {
  background-color: white;
}

.showcase {
  min-height: 200px;
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
  justify-content: space-between;
  padding: 50px 0 55px;
}

.showcase-item {
  text-align: center;
  width: 200px;
  color: white;
  background: var(--primary-blue);
  padding: 30px 25px 5px;
  border-radius: 15px;
  line-height: 1.5rem;
}

.showcase-item a {
  color: white;
  text-decoration: underline;
}

.showcase-item h5 {
  margin-bottom: 25px;
}

/*      RANDOM CATCH      */

.random-catch-container {
  align-items: center;
  flex-direction: column;
  padding-top: 50px;
  background-color: var(--primary-blue);
  color: white;
}

.random-catch-container h4 {
  margin-bottom: 40px;
}

.random-catch {
  width: 100%;
}

.random-catch-item-container {
  flex-wrap: wrap;
  width: 100%;
}

.random-catch-item {
  width: 25%;
  text-align: center;
}

.random-catch-item h6 {
  color: white;
  margin-bottom: 10px;
}

.random-catch-item img {
  width: 50%;
  height: calc(20vw * 0.5625);
  margin-bottom: 30px;
}

.random-catch-button {
  border: none;
  border-radius: 8px;
  padding: 10px 50px;
  margin: 10px 0 50px;
  cursor: pointer;
  color: var(--primary-orange);
  font-weight: bold;
}

.random-catch-button:active {
  border: 1px solid white;
  background-color: var(--primary-orange);
  color: white;
}

/*      FOOTER      */

.footer {
  padding-top: 150px;
  background-color: rgba(0, 0, 0, 0.322);
}

/*      FISH INFO CONTAINER     */

.fish-info-container {
  max-width: 1500px;
  width: 80%;
  margin: 0 auto;
  color: white;
}

.info-name {
  margin: 35px 0 65px;
}

.info-name h2 {
  margin-bottom: 7px;
}

.info-name h6 {
  font-style: italic;
  font-weight: lighter;
  font-size: 0.95rem;
}

.info-item {
  padding-bottom: 25px;
  margin-bottom: 25px;
  border-bottom: 1px solid white;
  min-height: 150px;
}

.info-item ul {
  line-height: 1.5rem;
  list-style-type: circle;
  margin-left: 22px;
}

.info-item a {
  color: white;
  text-decoration: underline;
}

.info-item h3 {
  margin-bottom: 15px;
}

.info-images {
  border-bottom: none;
}

.info-images-list {
  flex-wrap: wrap;
  margin-left: 10px;
}

.info-images-list a {
  width: 18%;
  margin-right: 20px;
  margin-bottom: 20px;
}

.info-images-list img {
  width: 100%;
  height: calc(20vw * 0.5625);
}

.info-float-container {
  float: right;
  background-color: var(--primary-blue);
  width: 23%;
}

.info-float {
  background-color: white;
  width: 100%;
  height: 1000px;
  margin-left: 50px;
  border-radius: 15px;
}